

 /* HERO  */
.carousel {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 90vh;
}

.slide {
  border-radius: 0.5rem;
  box-shadow: 0px 0px 7px #666;
  width: 100%;
  height: 100%;

}

.slide-hidden {
  display: none;
}
.image-container{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  image-rendering: optimizeQuality;
}
.image-container::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
}
.slide-image{
  width: 100%;
  height: 100%;
  object-fit: cover;


}
.slide-content{
  position: absolute;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  color: #fff;

}

.slide-content .slide-text {
  margin: 0 auto 15px;
  max-width: 80%;
  /* letter-spacing: 0.35em; */
  /* text-transform: uppercase; */
  font-size: 64px;
  font-weight: 500;
  line-height: 78px;
  text-align: center;
}
.btn {
  margin-top: 18px;
  display: inline-block;
  padding: 13px 20px;
  color: #fff;
  text-decoration: none;
  position: relative;
  background: transparent;
  border: 1px solid #e1e1e1;
  font: 10px "Oswald", sans-serif;
  letter-spacing: 0.4em;
  text-align: center;
  text-indent: 2px;
  text-transform: uppercase;
  transition: color 0.1s linear 0.05s;
}
.btn::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 1px;
  background: #e1e1e1;
  z-index: 1;
  opacity: 0;
  transition: height 0.2s ease, top 0.2s ease, opacity 0s linear 0.2s;
}
.btn::after {
  transition: border 0.1s linear 0.05s;
}
.btn .btn-inner {
  position: relative;
  z-index: 2;
}
.btn:hover {
  color: #373737;
  transition: color 0.1s linear 0s;
}
.btn:hover::before {
  top: 0;
  height: 100%;
  opacity: 1;
  transition: height 0.2s ease, top 0.2s ease, opacity 0s linear 0s;
}
.btn:hover::after {
  border-color: #373737;
  transition: border 0.1s linear 0s;
}
.arrow {
  position: absolute;
  filter: drop-shadow(0px 0px 5px #555);
  width: 3rem;
  height: 3rem;
  color: rgb(236, 230, 230);
  z-index: 1;
}

.arrow:hover {
  cursor: pointer;
  transform: scale(0.8);
  transition: all .2s ease-in-out 0s;
  -webkit-transform: all .2s ease-in-out 0s;
}

.arrow-left {
  left: 2rem;

}

.arrow-right {
  right: 2rem;

}

.indicators {
  display: flex;
  position: absolute;
  bottom: 1rem;
}

.indicator {
  background-color: white;
  height: 0.15rem;
  width: 4rem;
  border: none;
  outline: none;
  box-shadow: 0px 0px 5px #555;
  margin: 0 0.2rem;
  cursor: pointer;
}

.indicator-inactive {
  background-color: grey;
}
 
@media (max-width: 1200px) {
  .slide-content .slide-text {

    font-size: 58px;
    font-weight: 500;
    line-height: 68px;
  }
}

@media (max-width: 992px) {
  .slide-content .slide-text {
    font-size: 54px;
    font-weight: 500;
    line-height: 66px;
  }
  .arrow {

    width: 2.5rem;
    height: 2.5rem;

  }
}
@media (max-width: 768px) {
  .slide-content .slide-text {
    font-size: 42px;
    font-weight: 500;
    line-height: 54px;
  }

  .arrow-left {
    left: 1.5rem;
  
  } 
  .arrow-right {
    right: 1.5rem;
  
  }
}
@media (max-width: 680px) {
  .slide-content .slide-text {
    font-size: 32px;
    font-weight: 500;
    line-height: 54px;
  }

  .arrow-left {
    left: 0.5rem;
  
  } 
  .arrow-right {
    right: 0.5rem;
  }
  .indicator {
    height: 0.15rem;
    width: 3rem;
    margin: 0 0.5rem;

  }
}



