/*======================================
	01. Start Gallery CSS
========================================*/

/* Responsive image gallery rules begin*/

.image-gallery {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}


.image-gallery::after {
  content: "";
  flex-basis: 380px;
}
.image-gallery .image-grid {
  flex-basis: 380px; /*width: 350px;*/
  height: 200px;
  position: relative;
  cursor: pointer;
}

.image-gallery .image-grid img {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  /* max-width: 100%; */
  /* vertical-align: middle; */
  border-radius: 5px;
  /* height: auto; */


}

.overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(57, 57, 57, 0.502);
  top: 0;
  left: 0;
  transform: scale(0);
  transition: all 0.2s 0.1s ease-in-out;
  color: #fff;
  border-radius: 5px;
  /* center overlay text */
  display: flex;
  align-items: center;
  justify-content: center;
}

/* hover */
.image-gallery .image-grid:hover .overlay {
  transform: scale(1);
}



.image-viewer {
  position: fixed;
  background: rgba(0, 0, 0, 0.9);
  top: 10%;
  left: 10%;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  width: 840px;
  height: 620px;
}

.image-viewer img {
  max-width: 90%;
  max-height: 90%;
}

.prev-button,
.next-button {
  position: absolute;
  font-size: 2rem;
  color: #fff;
  background-color: transparent;
  border: none;
  cursor: pointer;
  top: 50%;
}
.close-button {
  position: absolute;
  font-size: 2rem;
  color: #fff;
  cursor: pointer;
  top: 0%;
}

.close-button {
  right: 15px;
}

.prev-button {
  left: 15px;
}

.next-button {
  right: 15px;
}

