.card {
  max-width: 320px;
  margin: 16px auto;
  /* overflow-y: auto; */
  position: relative;
  z-index: 1;
  /* overflow-x: hidden; */
  background-color: rgba(255, 255, 255, 1);
  display: flex;
  transition: 0.3s;
  flex-direction: column;
  border-radius: 10px;
  box-shadow: 0 0 0 8px rgba(255, 255, 255, 0.2);
  padding: 0px;
}
.card.is-active .card-header {
  height: 80px;
}
.card.is-active .card-cover {
  height: 100px;
  top: -50px;
}
.card.is-active .card-avatar {
  transform: none;
  left: 20px;
  width: 50px;
  height: 50px;
  bottom: 10px;
}
.card.is-active .card-fullname, .card.is-active .card-jobtitle {
  /* left: 86px; */
  /* transform: none; */
}
.card.is-active .card-fullname {
  /* bottom: 18px; */
  font-size: 19px;
}
.card.is-active .card-jobtitle {
  letter-spacing: 1px;
  font-size: 10px;
}
.card-header {
  position: relative;
  display: flex;
  height: 220px;
  flex-shrink: 0;
  width: 100%;
  transition: 0.3s;
  background-color: #f1fff9;
}
.card-header * {
  transition: 0.3s;
}
